"use client";

import React, { useState } from "react";
import { imageToVideo } from "../lib/openrouterApi";

export default function ImageToVideo() {
  const [imageFile, setImageFile] = useState<File | null>(null);
  const [videoUrl, setVideoUrl] = useState<string | null>(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const apiKey = process.env.NEXT_PUBLIC_OPENROUTER_API_KEY || "";

  const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (e.target.files && e.target.files.length > 0) {
      setImageFile(e.target.files[0]);
      setVideoUrl(null);
      setError(null);
    }
  };

  const generateVideo = async () => {
    if (!imageFile) return;
    setLoading(true);
    setError(null);
    setVideoUrl(null);

    try {
      // Upload image to get a URL (simulate or implement actual upload)
      // For now, use URL.createObjectURL for demo (replace with real upload in prod)
      const imageUrl = URL.createObjectURL(imageFile);

      const response = await imageToVideo(apiKey, imageUrl);
      if (response.data && response.data.length > 0) {
        setVideoUrl(response.data[0].url);
      } else {
        setError("No video generated");
      }
    } catch (err: any) {
      setError(err.message || "Error generating video");
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="p-4 border rounded-md border-gray-300 dark:border-gray-700 flex flex-col max-w-xl">
      <h2 className="text-xl font-semibold mb-4">Image to Video</h2>
      <input
        type="file"
        accept="image/*"
        onChange={handleFileChange}
        disabled={loading}
        className="mb-4"
      />
      <button
        onClick={generateVideo}
        disabled={loading || !imageFile}
        className="bg-blue-600 text-white rounded-md px-4 py-2 disabled:opacity-50 hover:bg-blue-700 transition mb-4"
      >
        {loading ? "Generating..." : "Generate Video"}
      </button>
      {error && <div className="text-red-600 mb-2">{error}</div>}
      {videoUrl && (
        <video
          src={videoUrl}
          controls
          className="rounded-md border border-gray-300 dark:border-gray-700 max-w-full"
        />
      )}
    </div>
  );
}
