"use client";

import React, { useState, useEffect } from "react";
import { Sun, Moon } from "lucide-react";
import ChatGPTChat from "../components/ChatGPTChat";
import TextToImage from "../components/TextToImage";
import ImageToVideo from "../components/ImageToVideo";
import TextToVoice from "../components/TextToVoice";
import VoiceChat from "../components/VoiceChat";
import Settings from "../components/Settings";

const tabs = [
  { id: "chatgpt", label: "ChatGPT" },
  { id: "texttoimage", label: "Text to Image" },
  { id: "imagetovideo", label: "Image to Video" },
  { id: "texttovoice", label: "Text to Voice" },
  { id: "voicechat", label: "Voice Chat AI" },
  { id: "settings", label: "Settings" },
];

export default function HomePage() {
  const [activeTab, setActiveTab] = useState("chatgpt");
  const [darkMode, setDarkMode] = useState(false);
  const [chatApiKey, setChatApiKey] = useState("");
  const [imageApiKey, setImageApiKey] = useState("");

  useEffect(() => {
    const savedKey = localStorage.getItem("chatApiKey") || "";
    setChatApiKey(savedKey);
    const savedImageKey = localStorage.getItem("imageApiKey") || "";
    setImageApiKey(savedImageKey);
  }, []);

  const toggleTheme = () => {
    setDarkMode(!darkMode);
    if (!darkMode) {
      document.documentElement.classList.add("dark");
    } else {
      document.documentElement.classList.remove("dark");
    }
  };

  const handleApiKeyChange = (key: string) => {
    setChatApiKey(key);
    localStorage.setItem("chatApiKey", key);
  };

  const handleImageApiKeyChange = (key: string) => {
    setImageApiKey(key);
    localStorage.setItem("imageApiKey", key);
  };

  return (
    <div className={`min-h-screen ${darkMode ? "bg-gray-900 text-white" : "bg-white text-gray-900"} transition-colors duration-500`}>
      <header className="flex items-center justify-between p-4 border-b border-gray-300 dark:border-gray-700">
        <h1 className="text-2xl font-bold">STUDIO AI DEVILL</h1>
        <button
          onClick={toggleTheme}
          aria-label="Toggle Dark Mode"
          className="p-2 rounded hover:bg-gray-200 dark:hover:bg-gray-700 transition"
        >
          {darkMode ? <Sun className="w-6 h-6" /> : <Moon className="w-6 h-6" />}
        </button>
      </header>

      <main className="flex flex-col md:flex-row min-h-[calc(100vh-64px)]">
        <nav className="flex md:flex-col border-b md:border-b-0 md:border-r border-gray-300 dark:border-gray-700">
          {tabs.map((tab) => (
            <button
              key={tab.id}
              onClick={() => setActiveTab(tab.id)}
              className={`px-4 py-3 md:py-4 md:px-6 font-semibold focus:outline-none transition-colors ${
                activeTab === tab.id
                  ? "border-b-2 md:border-b-0 md:border-l-4 border-blue-600 dark:border-blue-400 text-blue-600 dark:text-blue-400"
                  : "hover:bg-gray-100 dark:hover:bg-gray-800"
              }`}
            >
              {tab.label}
            </button>
          ))}
        </nav>

        <section className="flex-1 p-6 overflow-auto">
          {activeTab === "chatgpt" && (chatApiKey ? <ChatGPTChat apiKey={chatApiKey} /> : <p className="text-red-600">Please enter and save your API key in Settings to use ChatGPT.</p>)}
          {activeTab === "texttoimage" && (imageApiKey ? <TextToImage apiKey={imageApiKey} /> : <p className="text-red-600">Please enter and save your API key in Settings to use Text to Image.</p>)}
          {activeTab === "imagetovideo" && <ImageToVideo />}
          {activeTab === "texttovoice" && <TextToVoice />}
          {activeTab === "voicechat" && <VoiceChat />}
          {activeTab === "settings" && <Settings apiKey={chatApiKey} onApiKeyChange={handleApiKeyChange} imageApiKey={imageApiKey} onImageApiKeyChange={handleImageApiKeyChange} />}
        </section>
      </main>
    </div>
  );
}
