"use client";

import React, { useState, useRef } from "react";
import { textToVoice } from "../lib/openrouterApi";

export default function TextToVoice() {
  const [text, setText] = useState("");
  const [audioUrl, setAudioUrl] = useState<string | null>(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const audioRef = useRef<HTMLAudioElement>(null);

  const apiKey = process.env.NEXT_PUBLIC_OPENROUTER_API_KEY || "";

  const synthesizeVoice = async () => {
    if (!text.trim()) return;
    setLoading(true);
    setError(null);
    setAudioUrl(null);

    try {
      const response = await textToVoice(apiKey, text);
      if (response.data && response.data.length > 0) {
        setAudioUrl(response.data[0].url);
      } else {
        setError("No audio generated");
      }
    } catch (err: any) {
      setError(err.message || "Error generating audio");
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="p-4 border rounded-md border-gray-300 dark:border-gray-700 flex flex-col max-w-xl">
      <h2 className="text-xl font-semibold mb-4">Text to Voice</h2>
      <textarea
        className="resize-none border rounded-md p-2 mb-2 dark:bg-gray-900 dark:text-white"
        rows={3}
        placeholder="Enter text to synthesize"
        value={text}
        onChange={(e) => setText(e.target.value)}
        disabled={loading}
      />
      <button
        onClick={synthesizeVoice}
        disabled={loading}
        className="bg-blue-600 text-white rounded-md px-4 py-2 disabled:opacity-50 hover:bg-blue-700 transition mb-4"
      >
        {loading ? "Synthesizing..." : "Synthesize Voice"}
      </button>
      {error && <div className="text-red-600 mb-2">{error}</div>}
      {audioUrl && (
        <audio controls ref={audioRef} src={audioUrl} className="w-full rounded-md" />
      )}
    </div>
  );
}
