"use client";

import React, { useState, useRef, useEffect } from "react";
import { voiceChat } from "../lib/openrouterApi";

export default function VoiceChat() {
  const [recording, setRecording] = useState(false);
  const [audioUrl, setAudioUrl] = useState<string | null>(null);
  const [error, setError] = useState<string | null>(null);
  const mediaRecorderRef = useRef<MediaRecorder | null>(null);
  const audioChunksRef = useRef<Blob[]>([]);
  const audioRef = useRef<HTMLAudioElement>(null);

  const apiKey = process.env.NEXT_PUBLIC_OPENROUTER_API_KEY || "";

  useEffect(() => {
    if (!navigator.mediaDevices || !navigator.mediaDevices.getUserMedia) {
      setError("Media devices API not supported in this browser.");
    }
  }, []);

  const startRecording = async () => {
    setError(null);
    setAudioUrl(null);
    try {
      const stream = await navigator.mediaDevices.getUserMedia({ audio: true });
      mediaRecorderRef.current = new MediaRecorder(stream);
      audioChunksRef.current = [];

      mediaRecorderRef.current.ondataavailable = (event) => {
        if (event.data.size > 0) {
          audioChunksRef.current.push(event.data);
        }
      };

      mediaRecorderRef.current.onstop = async () => {
        const audioBlob = new Blob(audioChunksRef.current, { type: "audio/webm" });
        await sendAudioToApi(audioBlob);
      };

      mediaRecorderRef.current.start();
      setRecording(true);
    } catch (err: any) {
      setError(err.message || "Error accessing microphone");
    }
  };

  const stopRecording = () => {
    if (mediaRecorderRef.current && recording) {
      mediaRecorderRef.current.stop();
      setRecording(false);
    }
  };

  const sendAudioToApi = async (audioBlob: Blob) => {
    setError(null);
    setAudioUrl(null);
    try {
      const response = await voiceChat(apiKey, audioBlob);
      if (response.data && response.data.length > 0) {
        setAudioUrl(response.data[0].url);
      } else {
        setError("No audio response from AI");
      }
    } catch (err: any) {
      setError(err.message || "Error sending audio to API");
    }
  };

  return (
    <div className="p-4 border rounded-md border-gray-300 dark:border-gray-700 flex flex-col max-w-xl">
      <h2 className="text-xl font-semibold mb-4">Voice Chat AI</h2>
      {error && <div className="text-red-600 mb-2">{error}</div>}
      <div className="mb-4">
        {!recording ? (
          <button
            onClick={startRecording}
            className="bg-green-600 text-white rounded-md px-4 py-2 hover:bg-green-700 transition"
          >
            Start Recording
          </button>
        ) : (
          <button
            onClick={stopRecording}
            className="bg-red-600 text-white rounded-md px-4 py-2 hover:bg-red-700 transition"
          >
            Stop Recording
          </button>
        )}
      </div>
      {audioUrl && (
        <audio controls ref={audioRef} src={audioUrl} className="w-full rounded-md" />
      )}
    </div>
  );
}
