"use client";

import React, { useState, useEffect, useRef } from "react";
import { chatCompletion } from "../lib/openrouterApi";

interface Message {
  role: "user" | "assistant";
  content: string;
}

interface ChatGPTChatProps {
  apiKey: string;
}

export default function ChatGPTChat({ apiKey }: ChatGPTChatProps) {
  const [messages, setMessages] = useState<Message[]>([]);
  const [input, setInput] = useState("");
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const messagesEndRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
  }, [messages]);

  const sendMessage = async () => {
    if (!input.trim()) return;
    setLoading(true);
    setError(null);

    const newMessages: Message[] = [...messages, { role: "user", content: input }];
    setMessages(newMessages);
    setInput("");

    try {
      const response = await chatCompletion(apiKey, newMessages);
      if (response.choices && response.choices.length > 0) {
        const assistantMessage = response.choices[0].message;
        setMessages((prev) => [...prev, { role: "assistant", content: assistantMessage.content }]);
      } else {
        setError("No response from AI");
      }
    } catch (err: any) {
      setError(err.message || "Error calling API");
    } finally {
      setLoading(false);
    }
  };

  const handleKeyDown = (e: React.KeyboardEvent) => {
    if (e.key === "Enter" && !e.shiftKey) {
      e.preventDefault();
      sendMessage();
    }
  };

  return (
    <div className="flex flex-col h-full max-h-[600px] border rounded-md border-gray-300 dark:border-gray-700 p-4">
      <h2 className="text-xl font-semibold mb-4">ChatGPT</h2>
      <div className="flex-1 overflow-y-auto mb-4 space-y-3">
        {messages.map((msg, idx) => (
          <div
            key={idx}
            className={`p-3 rounded-md max-w-[80%] ${
              msg.role === "user"
                ? "bg-blue-100 text-blue-900 self-end dark:bg-blue-900 dark:text-blue-100"
                : "bg-gray-200 text-gray-900 self-start dark:bg-gray-800 dark:text-gray-100"
            }`}
          >
            {msg.content}
          </div>
        ))}
        <div ref={messagesEndRef} />
      </div>
      {error && <div className="text-red-600 mb-2">{error}</div>}
      <textarea
        className="resize-none border rounded-md p-2 mb-2 dark:bg-gray-900 dark:text-white"
        rows={3}
        value={input}
        onChange={(e) => setInput(e.target.value)}
        onKeyDown={handleKeyDown}
        placeholder="Type your message and press Enter"
        disabled={loading}
      />
      <button
        onClick={sendMessage}
        disabled={loading}
        className="bg-blue-600 text-white rounded-md px-4 py-2 disabled:opacity-50 hover:bg-blue-700 transition"
      >
        {loading ? "Sending..." : "Send"}
      </button>
    </div>
  );
}
