"use client";

import React, { useState, useEffect } from "react";

interface SettingsProps {
  apiKey: string;
  onApiKeyChange: (key: string) => void;
  imageApiKey: string;
  onImageApiKeyChange: (key: string) => void;
}

export default function Settings({ apiKey, onApiKeyChange, imageApiKey, onImageApiKeyChange }: SettingsProps) {
  const [inputKey, setInputKey] = useState(apiKey);
  const [inputImageKey, setInputImageKey] = useState(imageApiKey);
  const [isApiKeySaved, setIsApiKeySaved] = useState(!!apiKey);
  const [isImageApiKeySaved, setIsImageApiKeySaved] = useState(!!imageApiKey);

  useEffect(() => {
    setInputKey(apiKey);
    setIsApiKeySaved(!!apiKey);
  }, [apiKey]);

  useEffect(() => {
    setInputImageKey(imageApiKey);
    setIsImageApiKeySaved(!!imageApiKey);
  }, [imageApiKey]);

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setInputKey(e.target.value);
  };

  const handleImageKeyChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setInputImageKey(e.target.value);
  };

  const handleSave = () => {
    onApiKeyChange(inputKey.trim());
    setIsApiKeySaved(true);
  };

  const handleImageSave = () => {
    onImageApiKeyChange(inputImageKey.trim());
    setIsImageApiKeySaved(true);
  };

  const handleEditApiKey = () => {
    setIsApiKeySaved(false);
  };

  const handleEditImageApiKey = () => {
    setIsImageApiKeySaved(false);
  };

  return (
    <div className="p-4 border rounded-md border-gray-300 dark:border-gray-700 max-w-xl space-y-6">
      <div>
        <h2 className="text-xl font-semibold mb-4">Settings</h2>
        <label htmlFor="apiKey" className="block mb-2 font-medium">
          Chat AI API Key
        </label>
        {isApiKeySaved ? (
          <div className="flex items-center space-x-4">
            <input
              id="apiKey"
              type="password"
              value="************"
              readOnly
              className="w-full p-2 border rounded-md mb-4 dark:bg-gray-900 dark:text-white cursor-not-allowed"
            />
            <button
              onClick={handleEditApiKey}
              className="bg-yellow-500 text-white rounded-md px-4 py-2 hover:bg-yellow-600 transition"
            >
              Edit
            </button>
          </div>
        ) : (
          <>
            <input
              id="apiKey"
              type="text"
              value={inputKey}
              onChange={handleChange}
              placeholder="Enter your OpenRouter API key"
              className="w-full p-2 border rounded-md mb-4 dark:bg-gray-900 dark:text-white"
            />
            <button
              onClick={handleSave}
              className="bg-blue-600 text-white rounded-md px-4 py-2 hover:bg-blue-700 transition"
            >
              Save API Key
            </button>
          </>
        )}
      </div>

      <div>
        <label htmlFor="imageApiKey" className="block mb-2 font-medium">
          Text to Image API Key
        </label>
        {isImageApiKeySaved ? (
          <div className="flex items-center space-x-4">
            <input
              id="imageApiKey"
              type="password"
              value="************"
              readOnly
              className="w-full p-2 border rounded-md mb-4 dark:bg-gray-900 dark:text-white cursor-not-allowed"
            />
            <button
              onClick={handleEditImageApiKey}
              className="bg-yellow-500 text-white rounded-md px-4 py-2 hover:bg-yellow-600 transition"
            >
              Edit
            </button>
          </div>
        ) : (
          <>
            <input
              id="imageApiKey"
              type="text"
              value={inputImageKey}
              onChange={handleImageKeyChange}
              placeholder="Enter your Text to Image API key"
              className="w-full p-2 border rounded-md mb-4 dark:bg-gray-900 dark:text-white"
            />
            <button
              onClick={handleImageSave}
              className="bg-blue-600 text-white rounded-md px-4 py-2 hover:bg-blue-700 transition"
            >
              Save Image API Key
            </button>
          </>
        )}
      </div>
    </div>
  );
}
